@media (max-width: 1440px) {
  .ty-button {
    font-size: 20px;

    padding: 14px 26px;
  }

  .start-screen {
    &__title {
      font-size: 50px;

      margin-top: 120px;
      margin-bottom: 20px;
    }
    &__content {
      font-size: 18px;
      line-height: 22px;
      margin-bottom: 20px;
    }
    &__controls {
      margin-bottom: 40px;
    }

    &__footer {
      font-size: 14px;
      line-height: 22px;
    }
  }

  .stepper {
    &__title {
      font-size: 30px;
    }

    &__bullets {
      margin-bottom: 15px;

      &-item {
        &::before {
          border-bottom: 6px solid #d0d0d0;
          top: 14px;
        }
        &::after {
          border-bottom: 6px solid #d0d0d0;
          top: 14px;
        }

        &-counter {
          margin-bottom: 6px;
          width: 36px;
          height: 36px;
          font-size: 22px;
          border: 2px solid #fff;
        }
      }
    }

    &__content {
      margin-bottom: 30px;
    }
    &__footer {
      text-align: center;

      & > * {
        &:not(:last-child) {
          margin-right: 15px;
        }
      }
    }
  }

  .finish-screen {
    &__title {
      font-size: 20px;
      margin-bottom: 15px;
    }
    &__text {
      font-size: 16px;
      margin-bottom: 20px;
    }
  }

  .input-tag {
    border-radius: 10px;
    background: #fafafa;
    border: 1px solid #e8e8e8;

    font-weight: 500;
    color: #da291c;
  }

  #resultScreen {
    .print-logo {
      img {
        width: 110px;
      }
    }
    .result {
      &__header {
        width: 750px;
        margin-bottom: 15px;
      }
      &__title {
        font-size: 20px;
      }
      &__footer {
        display: none;
      }
    }
  }
  #resultContainer {
    // transform: scale(0.5);
    // transform-origin: 0 0;
    width: 750px;
    .result {
      &__content {
        font-size: 14px;

        &-title {
          font-size: 24px;
          margin-bottom: 10px;
        }
        &-subtitle {
          font-size: 16px;
          margin-top: 10px;
          margin-bottom: 20px;
        }

        p {
          margin-bottom: 15px;
        }
      }
    }
    .input-tag {
      padding: 5px 15px;
      font-size: 15px;
    }
  }

  #resultScreen:not(.printing) {
    .print-logo {
      display: none;
    }
    .result {
      &__header {
        width: 100%;
        margin-bottom: 40px;
      }
      &__title {
        font-size: 30px;
      }
      &__footer {
        display: block;
      }
    }
  }
  #resultScreen:not(.printing) {
    #resultContainer {
      width: 100%;
      .result {
        &__content {
          font-size: 20px;

          &-title {
            font-size: 30px;
            margin-bottom: 10px;
          }
          &-subtitle {
            font-size: 30px;

            margin-top: 20px;
            margin-bottom: 20px;
          }

          p {
            margin-bottom: 20px;
          }
        }
      }
      .input-tag {
        padding: 5px 15px;
        font-size: 20px;
      }
    }
  }
}
@media (max-width: 1366px) {
  .main-header__top,
  .main-content {
    padding-left: 50px;
    padding-right: 50px;
  }
  .main-header__content {
    padding-left: 0;
    padding-right: 0;
  }
}
@media (max-width: 1200px) {
}
@media (max-width: 1100px) {
  .main-header {
    .panel-content {
      &--search {
        padding-top: 90px;
      }
      &--language {
        padding-top: 90px;
      }
    }
  }
}
@media (max-width: 992px) {
  .main-header__top,
  .main-content {
    padding-left: 10px;
    padding-right: 10px;
  }
  .main-header {
    &__logo {
      width: 126px;
    }
    &__content {
      padding: 0 0;
    }
    &__nav-wrap {
      display: none;
    }
    &__languages-btn {
      display: none;
    }

    &__overlay-panel {
      top: 0 !important;
      padding: 0;
    }
    &__overlay-backdrop {
      top: 0 !important;
    }

    &__top {
      z-index: 890;
    }
    &__mobile-burger {
      display: block;
    }

    .panel-content {
      &--search {
        padding-top: 10px;
        padding-bottom: 10px;
        padding-left: 26px;
        padding-right: 8px;
      }

      &__title {
        display: none;
      }
      &__btn-close {
        border-radius: 0;
        background: none;
        i {
          display: none;
        }
        span {
          display: inline-block;
          color: #68737a;
          font-size: 14px;
          line-height: 16px;
          font-style: italic;
          font-weight: 600;
        }
      }
    }
    .panel-search-form {
      &__content {
        padding: 0;
        border: none;
      }
      &__input {
        padding-left: 13px;
      }

      &.active {
        .panel-search-form__content {
          box-shadow: none;
        }
      }
    }

    &.sticky {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      z-index: 999;
      .main-header__top {
        transform: none;
      }
    }
  }

  .body-freeze {
    border-right-width: 0;
  }

  .ty-button {
    font-size: 18px;

    padding: 16px 34px;
    min-width: 220px;

    // &--slim {
    //   font-size: 20px;
    //   padding: 10px 18px;
    // }
  }
  .ty-custom-select-wrap {
    width: 100%;
  }
  .ty-custom-select {
    padding: 16px 34px;
    width: 100%;
    font-size: 18px;
    padding-left: 70px;
    padding-right: 50px;

    &__icon-pre {
      left: 26px;
      font-size: 30px;
    }
    &__icon-after {
      right: 26px;
      font-size: 10px;
    }
  }

  .start-screen {
    &__title {
      font-size: 50px;

      margin-top: 80px;
      margin-bottom: 20px;
    }
    &__content {
      font-weight: 300;
      font-size: 16px;
      line-height: 22px;
      margin-bottom: 20px;
    }
    &__controls {
      margin-bottom: 30px;
    }
    &__control-item {
      margin-bottom: 15px;
      &:not(:last-child) {
        margin-right: 0;
      }

      width: 100%;
      .ty-button {
        width: 100%;
      }
      .ty-custom-select-wrap {
        width: 100%;
      }
    }
    &__footer {
      font-weight: 300;
      font-size: 14px;
      line-height: 22px;
    }
  }

  .field-wrapper {
    flex-direction: column;
    align-items: flex-start;

    margin-bottom: 25px;

    &--vertical {
      .field-label {
        width: 100%;
        margin-bottom: 20px;
      }
    }
  }
  .filed-label-wrap {
    width: 100%;
  }
  .field-label {
    width: 100%;
    margin-bottom: 10px;

    font-size: 14px;

    margin-right: 0;

    span {
      margin-right: 20px;
    }

    &__icon {
      font-size: 30px;
      color: #da291c;
      margin-right: 20px;
    }
  }

  .field-currency-wrap {
    max-width: none;
  }
  .field-number-wrap {
    max-width: none;
  }
  .field-wrap {
    max-width: none;
  }
  .field-select-wrap {
    max-width: none;
  }

  .column-structure {
    flex-wrap: wrap;
    &__desc {
      width: 100%;
      margin-right: 0;
      margin-bottom: 10px;
    }
    &__content {
      width: 100%;
    }
  }
  .view-chart-btn {
    font-size: 30px;
  }

  .field-double {
    &__item {
      width: 100%;
      &:last-child {
        display: none;
      }
      &:not(:last-child) {
        margin-right: 0;
      }
    }
  }
}
@media (max-width: 768px) {
  .stepper {
    &__header {
      margin-bottom: 20px;
    }
    &__title {
      font-size: 20px;
    }

    &__bullets {
      width: 100%;
      margin-bottom: 20px;

      &-item {
        &::before {
          position: absolute;
          content: "";
          border-bottom: 4px solid #d0d0d0;
          width: 100%;
          top: 14px;
          left: -50%;
          z-index: 2;
        }
        &::after {
          position: absolute;
          content: "";
          border-bottom: 4px solid #d0d0d0;
          width: 100%;
          top: 14px;
          left: 50%;
          z-index: 2;
        }

        &-counter {
          position: relative;
          z-index: 5;
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 50%;
          background: #d0d0d0;
          margin-bottom: 6px;

          color: #fff;
          width: 30px;
          height: 30px;
          font-size: 16px;
          font-weight: bold;
          border: 2px solid #fff;
        }

        &.completed {
          .stepper__bullets-item-counter {
            background-color: #da291c;
          }
          &::after {
            border-color: #da291c;
            z-index: 3;
          }
        }
        &.active {
          &::before {
            z-index: 3;
          }
        }

        &:first-child::before {
          content: none;
        }
        &:last-child::after {
          content: none;
        }
      }
    }

    &__content {
      margin-bottom: 30px;
    }
    &__footer {
      flex-wrap: wrap;
      & > * {
        width: 100%;
        margin-bottom: 15px;
        &:not(:last-child) {
          margin-right: 0;
        }
      }
    }
  }
  .start-screen {
    &__title {
      font-size: 32px;
    }
  }

  .finish-screen {
    &__title {
      font-size: 18px;
      margin-bottom: 20px;
    }
    &__total {
      font-size: 22px;

      padding: 5px 10px;

      margin-bottom: 20px;
    }
    &__controls {
      margin-bottom: 30px;
      flex-wrap: wrap;

      & > * {
        width: 100%;
        margin-bottom: 15px;
        &:not(:last-child) {
          margin-right: 0;
        }
      }
    }
    &__text {
      font-weight: normal;
      font-size: 14px;
      color: #505050;
      margin-bottom: 30px;
    }
    &__contacts {
      & > * {
        width: 100%;
        margin-bottom: 15px;
        &:not(:last-child) {
          margin-right: 0;
        }
      }
    }
  }

  .result {
    &__header {
      margin-bottom: 20px;
    }
    &__title {
      font-size: 20px;
    }
    &__footer {
      & > * {
        &:not(:last-child) {
          margin-right: 15px;
        }
      }
    }
  }

  #resultContainer {
    width: 100%;
  }

  #resultScreen:not(.printing) {
    #resultContainer {
      width: 100%;
      .result {
        &__content {
          font-size: 14px;
          &-title {
            word-break: break-all;
            font-size: 18px;
          }
          &-subtitle {
            word-break: break-all;
            font-size: 16px;
          }
        }
      }
      .input-tag {
        padding: 5px 15px;
        font-size: 20px;
      }
    }
  }
}
@media (max-width: 576px) {
  .overlay-cdk {
    &__content {
      padding: 20px;

      &-title {
        font-size: 24px;
        line-height: 1em;
      }
      &-footer {
        flex-wrap: wrap;
        .ty-button {
          width: 100%;
          margin-bottom: 15px;
        }
      }
    }
    &__close-btn {
      font-size: 24px;
      top: -24px;
      right: 40px;
    }
  }
}
